@charset "UTF-8";
@font-face {
  font-family: "NotoSans-Black";
  src: url("../font/NotoSans-Black.eot");
  /* IE 6 ~ 8, IE 호환성 보기 */
  src: local(☺), url("../font/NotoSans-Black.woff2") format("woff2"), url("../font/NotoSans-Black.woff") format("woff"), url("../font/NotoSans-Black.ttf") format("truetype"); }

.fonts-load {
  visibility: hidden; }

.fonts-loaded .fonts-load {
  visibility: visible; }
